import 'app.scss';
import Home from 'pages/Home/Home';
import { BrowserRouter, Route } from 'react-router-dom';

//Components
import Header from 'components/Header/Header';
import Login from 'pages/Login/Login';
import Register from 'pages/Register/Register';
import Account from 'pages/Account/Account';
import RechercheAvancee from 'pages/Recherche/RechercheAvancee/RechercheAvancee';
import ResultatsRecherche from 'pages/Recherche/ResultatsRecherche/ResultatsRecherche';

import SauveteurList from "pages/Sauveteur/List/List";
import SauveteurInfo from "pages/Sauveteur/Info/Info";

import BateauList from "pages/Bateau/List/List";
import BateauInfo from "pages/Bateau/Info/Info";

import SauvetageList from "pages/Sauvetage/List/List";
import SauvetageInfo from "pages/Sauvetage/Info/Info";

function App() {

  return (
    <BrowserRouter>
      <Header />

      <Route path="/" exact>
        <Home />
      </Route>

      <Route path="/login" exact>
        <Login />
      </Route>

      <Route path="/register" exact>
        <Register />
      </Route>

      <Route path="/mon-compte" exact>
        <Account />
      </Route>

      <Route path="/recherche-avancee" exact>
        <RechercheAvancee />
      </Route>

      <Route path="/resultats/:keyword/:filter/:mindate/:maxdate" exacte>
        <ResultatsRecherche />
      </Route>

      <Route path="/sauveteur" exact>
        <SauveteurList />
      </Route>
      
      <Route path="/sauveteur/:id" exact>
        <SauveteurInfo />
      </Route>

      <Route path="/bateau" exact>
        <BateauList />
      </Route>
      
      <Route path="/bateau/:id" exact>
        <BateauInfo />
      </Route>

      <Route path="/sauvetage" exact>
        <SauvetageList />
      </Route>
      
      <Route path="/sauvetage/:id" exact>
        <SauvetageInfo />
      </Route>

    </BrowserRouter>
  );
}

export default App;
