import React, { useEffect, useState } from 'react'
import { useHistory } from 'react-router-dom';
import './RechercheAvancee.scss'
import axiosConfig from 'axiosConfig';

const RechercheAvancee = () => {

  const hist = useHistory();

  const dates = {
    min: 1700,
    max: 2100
  }
  const [keyword, setKeyword] = useState("");
  const [maxValue, setMaxValue] = useState((dates.min+dates.max)/2+1);
  const [minValue, setMinValue] = useState((dates.min+dates.max)/2);
  const [selectVal, setSelectVal] = useState(0);

  const handleMin = (e) => {
    if(e.target.value >= maxValue){
      setMaxValue(parseInt(e.target.value) + 1)
    }
    setMinValue(parseInt(e.target.value));
  }
  const handleMax = (e) => {
    if(e.target.value <= minValue){
      setMinValue(parseInt(e.target.value) - 1)
    }
    setMaxValue(parseInt(e.target.value));
  }

  const handleSubmit = () => {
    hist.push(`/resultats/${keyword}/${selectVal}/${minValue}/${maxValue}`)
  }

  return (
    <div className="page-form">
      <form onSubmit={handleSubmit}>
        <section>
          <div>
            <label>Recherche :</label>
            <input required type="text" value={keyword} placeholder="Mots clés" onChange={(e) => setKeyword(e.target.value)}></input>
          </div>
          <div>
            <label>Parmi :</label>
            <select value={selectVal} onChange={(e) => setSelectVal(parseInt(e.target.value))}>
              <option value="0">Tout</option>
              <option value="1">Les sauveteurs</option>
              <option value="2">Les navires</option>
              <option value="3">Les missions</option>
            </select>
          </div>
          <div>
            <input type="range" value={minValue} step="1" min={dates.min} max={dates.max} onChange={handleMin} className="slider"/>
            <input type="number" value={minValue} placeholder="Date minimum" min="1800" max="2100" onChange={handleMin}></input>
          </div>
          <div>
            <input type="range" value={maxValue} step="1" min={dates.min} max={dates.max} onChange={handleMax} className="slider"/>
            <input type="number" value={maxValue} placeholder="Date maximum" min="1800" max="2100" onChange={handleMax}></input>
          </div>
          <div className="flex-center">
            <button type="submit" className="btn btn-half">Rechercher</button>
          </div>
        </section>
      </form>
    </div>
  );
}

export default RechercheAvancee;