import React, { useEffect, useState, useContext } from 'react'
import './Info.scss'
import axiosConfig from 'axiosConfig';
import { Link, useParams } from 'react-router-dom';
import { UserContext } from 'context';
import { useHistory } from 'react-router-dom';
import { RiUserHeartFill } from "react-icons/ri";
import { GiShoonerSailboat as Bato} from 'react-icons/gi'
import Retour from 'components/Retour/Retour';

const Info = () => {
  const [nom, setNom] = useState("");
  const [sous_patron, setSous_patron] = useState("");
  const [patron, setPatron] = useState("");
  const [armement, setArmement] = useState("");
  const [user] = useContext(UserContext);
  const admin = true;
  const [mission, setMission] = useState([]);

  const { id } = useParams();
  const history = useHistory();

  const getObj = async () => {
    try {
      const result = await axiosConfig({
        method: 'get',
        url: `bateau/` + id,
      })

      console.log(result);
      if (result.data.err) {
        console.log(result.data.err);
      } else {
        const res = result.data;
        setNom(res.nom);
        setPatron(res.patron);
        setSous_patron(res.sous_patron);
        setArmement(res.armement);

        const result2 = await axiosConfig({
          method: 'post',
          url: "sauvetage/bateau",
          data: {
            id_bateau : res.id_bateau
          }
        })

        if (result2.data.err) {
          console.log(result.data);
        } else {
          setMission(result2.data);
          console.log(result2.data)
        }
      }
    } catch (e) { console.log(e) }
  }

  useEffect(() => {
    if (id != -1 && id) getObj();
  }, [id])

  const DisplayMission = () => {
    if (!mission.length) {
      return <p>Ce bateau n'a pas encore effectué de missions</p>
    }
    return mission.map((item, index) => {
      return <Link key={index} to={'sauvetage/' + item.id_sauvetage}>
        {item.nom}
      </Link>
    });
  }

  const post = async () => {
    try {
      const result = await axiosConfig({
        method: 'post',
        url: `bateau/`,
        data: {
          nom: nom,
          sous_patron: sous_patron,
          patron: patron,
          armement: armement,
          verif: 0,
          id_utilisateur: user.id
        } 
      })

      if (result.data.err) {
        console.log(result.data);
      } else {
        history.push("/bateau")
      }
    } catch (e) { console.log(e) }
  }

  const update = async () => {
    try {
      const result = await axiosConfig({
        method: 'put',
        url: `bateau/`,
        data: {
          id_bateau: id,
          nom: nom,
          sous_patron: sous_patron,
          patron: patron,
          armement: armement,
          verif: 0,
          id_utilisateur: user.id
        } 
      })

      if (result.data.err) {
        console.log(result.data.err);
      } else {
        getObj();
      }
    } catch (e) { console.log(e) }
  }

  const submit = (e) => {
    e.preventDefault();
    if (id != -1) {
      update();
    } else {
      post();
    }
  }

  return (
    <div className={(admin || id == -1) ? "page-form" : "page-form page-form-info"}>
      
      <form onSubmit={submit}>
        <h2>Information sur {nom}</h2>

        <Bato />
        <section>
          <div>
            <label htmlFor="nom">Nom</label>
            <input 
              type="text" 
              id="nom" 
              required 
              placeholder="nom"
              autocomplete="off"
              value={nom}
              onChange={e => setNom(e.target.value)}
              disabled={!admin}
            />
          </div>
          <div>
            <label htmlFor="patron">Patron</label>
            <input 
              type="text" 
              id="patron" 
              required 
              placeholder="patron"
              autocomplete="off"
              value={patron}
              onChange={e => setPatron(e.target.value)}
              disabled={!admin}
            />
          </div>

          <div>
            <label htmlFor="sous_patron">Sous-Patron</label>
            <input 
              type="text" 
              id="sous_patron" 
              required 
              placeholder="sous_patron"
              autocomplete="off"
              value={sous_patron}
              onChange={e => setSous_patron(e.target.value)}
              disabled={!admin}
            />
          </div>

          <div>
            <label htmlFor="armement">Armement</label>
            <input 
              type="text" 
              id="armement" 
              required 
              placeholder="armement"
              autocomplete="off"
              value={armement}
              onChange={e => setArmement(e.target.value)}
              disabled={!admin}
            />
          </div>
        </section>
        <div className="buttons">
        {(admin && id != -1) && <button className="btn" type="submit">Modifier</button>}
        {(id == -1) && <button className="btn" type="submit">Créer</button>}
        <Retour />
        </div>
        
      </form>

      {(id != -1) && <form onSubmit={submit}>
        <h2>Liste des sauvetages auquel {nom} à participer :</h2>

        <DisplayMission />
      </form>}
    </div>
    
  );
}

export default Info;