import React, { useEffect, useState, useContext } from 'react'
import './List.scss'
import axiosConfig from 'axiosConfig';
import { Link } from 'react-router-dom';
import { FaHandsHelping } from "react-icons/fa";
import { UserContext } from 'context';

const List = () => {
  const [user] = useContext(UserContext);
  const [list, setList] = useState([]);
  const admin = true;

  const getList = async () => {
    try {
      const result = await axiosConfig({
        method: 'get',
        url: `sauvetage`,
      })

      if (result.data.err) {
        console.log(result.data.err);
      } else {
        const tab = result.data.sort((a,b) => a.verif - b.verif)
        setList(tab);
      }
    } catch (e) { console.log(e) }
  }

  useEffect(() => {
    getList();
  }, [])

  const DisplayList = () => {

    return list.map((item, index) => {
      const del = async (e) => {
        e.stopPropagation();
        e.preventDefault();

        if (!window.confirm("Confirmer la suppression ?")) {
          return;
        }

        try {
          const result = await axiosConfig({
            method: 'delete',
            url: `id_sauvetage`,
            data: {
              id_sauvetage: item.id_sauvetage
            }
          })
  
          if (result.data.err) {
            console.log(result.data.err);
          } else {
            console.log(result.data);
            getList();
          }
        } catch (e) { console.log(e) }
      }

      return <Link className={item.verif ? "card" : " card card-verif"} to={`/sauvetage/` + item.id_sauvetage} key={index}>
        <FaHandsHelping />

        <p>{item.nom}</p>

        <div className="buttons">
          {admin && <>

            <p className="btn btn-red" onClick={del}>Supprimer</p>
            </>  
          }

          {(!admin) && <Link className="btn" to={`/sauvetage/` + item.id_sauvetage}>Voir plus</Link>}
          
        </div>
      </Link>
    })
  }

  return (
    <div className="page list">
      <div className="wrapper">
        <DisplayList />
      </div>
      
    </div>
  );
}

export default List;