import {Link} from "react-router-dom";
import React, { useContext, useState } from 'react';
import { UserContext } from 'context';
import "./NavbarDesktop.scss";
import { GiShoonerSailboat as Bato} from 'react-icons/gi'
import { BiSearchAlt as Search } from "react-icons/bi";

const NavbarDesktop = () => {
    const [user, setUser] = useContext(UserContext);
    const [active, setActive] = useState(0);

    return (
        <nav>
            <Link to="/">
                <h2>SDD</h2>
                <Bato />
            </Link>
            
            <ul>
                <li>
                    <Link className={active === 0 ? "btn btn-active" : "btn"} onClick={() => setActive(0)} to="/">Accueil</Link>
                </li>
                <li>
                    <Link className={active === 1 ? "btn btn-active" : "btn"} onClick={() => setActive(1)} to="/sauveteur">Sauveteurs</Link>
                </li>
                <li>
                    <Link className={active === 2 ? "btn btn-active" : "btn"} onClick={() => setActive(2)} to="/sauvetage">Sorties en mer</Link>
                </li>
                <li>
                    <Link className={active === 3 ? "btn btn-active" : "btn"} onClick={() => setActive(3)} to="/bateau">Navires</Link>
                </li>
                <li>
                    <Link className={active === 4 ? "btn btn-active" : "btn"} onClick={() => setActive(4)} to="/recherche-avancee">Recherche<Search /></Link>
                </li>
            </ul>
            {user ? <>
                <Link to="/mon-compte">Mon Compte</Link> 
                <Link onClick={() => { setUser(null); window.localStorage.removeItem("user")}} to="/login">Me Déconnecter</Link> 
            </>
            : <>
                <Link to="/login">Connexion</Link> 
                <Link to="/register">Inscription</Link> 
            </>}
        </nav>
    )
}

export default NavbarDesktop
