import './Register.scss';
import { useState, useEffect, useContext } from 'react';
import { Link } from 'react-router-dom';
import { UserContext } from 'context';
import axiosConfig from 'axiosConfig';
import { useHistory } from "react-router-dom";
function Register() {

  const history = useHistory();
  useEffect(() => {
    document.title = "S'inscrire";
  }, []);


  const verifyInput = async (event) => {
    event.preventDefault();
  
    let mail = document.getElementById("mail").value;
    let mdp = document.getElementById("mdp").value;
    let tel = document.getElementById("tel").value;
    let prenom = document.getElementById("prenom").value;
    let nom = document.getElementById("nom").value;
    let adresse = document.getElementById("adresse").value;

    try {
      const result = await axiosConfig({
        method: 'post',
        url: `user`,
        data: {
          mail: mail,
          mdp: mdp,
          tel: tel,
          nom: nom,
          prenom: prenom,
          adresse: adresse
        }
      })

      if (result.data.err) {
        console.log(result.data);
      } else {
        alert("Votre compte a bien été créé");
        history.push("/login")
      }
    } catch (e) { console.log(e) }
  }

  return (
    <div className="page-form register">
      <form onSubmit={verifyInput}>
              <h2>Vos information</h2>

              <section>
                <div>
                  <label htmlFor="mail">Mail</label>
                  <input 
                    type="text" 
                    id="mail" 
                    required 
                    placeholder="mail"
                    autoComplete="mail"
                  />
                </div>


              <div>
                <label htmlFor="nom">Nom</label>
                <input 
                  required 
                  type="text" 
                  id="nom" 
                  placeholder="nom" 
                  autoComplete="current-name"
                />
              </div>


              <div>
                <label htmlFor="prenom">Prénom</label>
                <input 
                  required 
                  type="text" 
                  id="prenom" 
                  placeholder="prenom" 
                  autoComplete="current-firstname"
                />
              </div>

              <div>
                <label htmlFor="tel">Téléphone</label>
                <input 
                  required 
                  type="tel" 
                  id="tel" 
                  placeholder="Téléphone" 
                  autoComplete="current-tel"
                />
              </div>

              <div>
                <label htmlFor="adresse">Votre adresse</label>
                <input 
                  required 
                  type="text" 
                  id="adresse" 
                  placeholder="Votre adresse" 
                  autoComplete="current-address"
                />
              </div>

              <div>
                <label htmlFor="mdp">Mot de passe</label>
                <input 
                  minLength="8" 
                  required 
                  type="password" 
                  id="mdp" 
                  placeholder="Mot de passe" 
                  autoComplete="current-password"
                />
              </div>

              <div>
                <label htmlFor="confirm-password">Confirmer votre mot de passe</label>
                <input 
                  minLength="8" 
                  required 
                  type="password" 
                  id="confirm-password" 
                  placeholder="Mot de passe" 
                  autoComplete="current-password"
                />
              </div>

              </section>

              <button className='btn' type="submit">S'inscrire</button>

              <div className="login-link">
                <Link to="/login">Vous avez déjà un compte ? Connectez-vous !</Link>
            </div>
      </form>

    </div>
  );
}

export default Register;
