import React, { useEffect, useState, useContext } from 'react'
import './List.scss'
import axiosConfig from 'axiosConfig';
import { Link } from 'react-router-dom';
import { RiUserHeartFill } from "react-icons/ri";
import { UserContext } from 'context';

const List = () => {
  const [user] = useContext(UserContext);
  const [list, setList] = useState([]);
  const admin = true;

  const getList = async () => {
    try {
      const result = await axiosConfig({
        method: 'get',
        url: `sauveteur`,
      })

      if (result.data.err) {
        console.log(result.data.err);
      } else {
        const tab = result.data.sort((a,b) => a.verif - b.verif)
        setList(tab);
      }
    } catch (e) { console.log(e) }
  }

  useEffect(() => {
    getList();
  }, [])

  const DisplayList = () => {

    return list.map((item, index) => {
      const del = async (e) => {
        e.stopPropagation();
        e.preventDefault();

        if (!window.confirm("Confirmer la suppression ?")) {
          return;
        }

        try {
          const result = await axiosConfig({
            method: 'delete',
            url: `sauveteur`,
            data: {
              id_sauveteur: item.id_sauveteur
            }
          })
  
          if (result.data.err) {
            console.log(result.data.err);
          } else {
            console.log(result.data);
            getList();
          }
        } catch (e) { console.log(e) }
      }

      const verif = async (e) => {
        e.stopPropagation();
        e.preventDefault();

        try {
          const result = await axiosConfig({
            method: 'put',
            url: `sauveteur`,
            data: {
              id_sauveteur: item.id_sauveteur,
              nom: item.nom,
              prenom: item.prenom,
              date_de_mort: item.date_de_mort,
              date_de_naissance: item.date_de_naissance,
              legion: item.legion,
              verif: 1,
              id_utilisateur: item.id_utilisateur
            }
          })
  
          if (result.data.err) {
            console.log(result.data.err);
          } else {
            console.log(result.data);
            getList();
          }
        } catch (e) { console.log(e) }
      }

      return <Link className={item.verif ? "card" : " card card-verif"} to={`sauveteur/` + item.id_sauveteur} key={index}>
        <RiUserHeartFill />

        <p>{item.nom}</p>

        <div className="buttons">
          {admin && <>
            {!item.verif && <p className="btn" onClick={verif}>Vérifier</p>}
            <p className="btn btn-red" onClick={del}>Supprimer</p>
            </>  
          }

          {(!admin) && <Link className="btn" to={`sauveteur/` + item.id_sauveteur}>Voir plus</Link>}
          
        </div>
      </Link>
    })
  }

  return (
    <div className="page list">
      <div className="head">
        <Link to={`sauveteur/` + -1} className="btn">Ajouter un sauveteur</Link>
      </div>
      <div className="wrapper">
        <DisplayList />
      </div>
      
    </div>
  );
}

export default List;