import './Login.scss';
import { useState, useEffect, useContext } from 'react';
import { Link } from 'react-router-dom';
import { UserContext } from 'context';
import axiosConfig from 'axiosConfig';
import { useHistory } from 'react-router-dom';

function Login() {
  const [keep, setKeep] = useState(true);
  const [, setUser] = useContext(UserContext);

  const history = useHistory();

  useEffect(() => {
    document.title = "Se connecter";
  }, []);

  const authUser = async (mail, password) => {
    try {
      const result = await axiosConfig({
        method: 'post',
        url: `user/auth`,
        data: {
          mail: mail,
          mdp: password
        }
      })

      if (result.data.err) {
        console.log(result.data.err);
      } else {
        const user = result.data;
        window.localStorage.setItem("user", JSON.stringify(user))
        setUser(user);
        history.push("/mon-compte")
      }
    } catch (e) { console.log(e) }
  
  }

  const verifyInput = (event) => {
    event.preventDefault();
  
    let mail = document.getElementById("mail").value;
    let password = document.getElementById("password").value;

    authUser(mail,password);
  }

  return (
    <div className="page-form login">
      <form onSubmit={verifyInput}>
              <h2>Veuillez entrer vos identifiants</h2>
              <section>
                <div className="login-arobase">
                  <label htmlFor="password">Email :</label>
                  <input 
                    type="text" 
                    id="mail" 
                    required 
                    placeholder="Email"
                    autoComplete="username"
                  />
                </div>

              <div className="login-password">
                <label htmlFor="password">Mot de passe :</label>
                <input 
                  minLength="8" 
                  required 
                  type="password" 
                  id="password" 
                  placeholder="Mot de passe" 
                  autoComplete="current-password"
                />
                
              </div>
              <div className="flex-start">
                
                  <input checked={keep} onChange={() => setKeep(!keep)} type="checkbox"  id='keep' />
                  <label htmlFor='keep'>Se souvenir de moi</label>
                
              </div>
              <div className="flex-center">
                <button className='btn-half btn' type="submit">Se connecter</button>
              </div>

              <div className="login-link flex-center">
                <Link to="/register">Pas de compte ? Inscrivez-vous !</Link>
                <Link to="/resetpassword">Mot de passe oublié ?</Link>
              </div>
            </section>
      </form>

    </div>
  );
}

export default Login;
