import React, { useEffect, useState, useContext } from 'react'
import './Info.scss'
import axiosConfig from 'axiosConfig';
import { Link, useParams } from 'react-router-dom';
import { UserContext } from 'context';
import { useHistory } from 'react-router-dom';
import { FaHandsHelping } from "react-icons/fa";
import Retour from 'components/Retour/Retour';

const Info = () => {
  const [nom, setNom] = useState("");
  const [content, setContent] = useState("");

  const { id } = useParams();

  const getObj = async () => {
    try {
      const result = await axiosConfig({
        method: 'get',
        url: `sauvetage/` + id,
      })

      if (result.data.err) {
        console.log(result.data.err);
      } else {
        const res = result.data;
        setNom(res.nom);
        setContent(res.content);
        setDate_arrive(res.date_arrive);
        setDate_depart(res.date_depart);
        setId_bateau(res.id_bateau);
      }

    } catch (e) { console.log(e) }
  }

  useEffect(() => {
    getObj();
  }, [id])


  const submit = (e) => {
    e.preventDefault();
    if (id != -1) {
      update();
    } else {
      post();
    }
  }

  return (
    <div className={"page-form page-form-info"}>
      <form onSubmit={submit}>
        <h2>Information sur la mission {nom}</h2>

        <FaHandsHelping />
        <section>
          <div>
            <label htmlFor="nom">Nom</label>
            <input 
              type="text" 
              id="nom" 
              required 
              placeholder="nom"
              autocomplete="off"
              value={nom}
              onChange={e => setNom(e.target.value)}
              disabled
            />
          </div>
          <div>
            <label htmlFor="content">Content</label>
            <input 
              type="text" 
              id="date_de_naissance" 
              required 
              placeholder="date de naissance"
              autocomplete="off"
              value={content}
              onChange={e => setContent(e.target.value)}
              disabled
            />
          </div>

        </section>

        <div className="buttons">
          <Retour />
        </div>
      </form>
    </div>
    
  );
}

export default Info;