import React, { useEffect, useState, useContext } from 'react'
import './Info.scss'
import axiosConfig from 'axiosConfig';
import { Link, useParams } from 'react-router-dom';
import { UserContext } from 'context';
import { useHistory } from 'react-router-dom';
import { RiUserHeartFill } from "react-icons/ri";
import Retour from 'components/Retour/Retour';

const Info = () => {
  const [nom, setNom] = useState("");
  const [prenom, setPrenom] = useState("");
  const [date_de_naissance, setDate_de_naissance] = useState("");
  const [date_de_mort, setDate_de_mort] = useState("");
  const [id_sauveteur, setId_sauveteur] = useState(0);
  const [legion, setLegion] = useState(false);
  const [user] = useContext(UserContext);
  const admin = true;
  const [mission, setMission] = useState([]);

  const { id } = useParams();
  const history = useHistory();

  const getObj = async () => {
    try {
      const result = await axiosConfig({
        method: 'get',
        url: `sauveteur/` + id,
      })

      if (result.data.err) {
        console.log(result.data.err);
      } else {
        const res = result.data;
        setNom(res.nom);
        setPrenom(res.prenom);
        setDate_de_mort(res.date_de_mort);
        setDate_de_naissance(res.date_de_naissance);
        setId_sauveteur(res.id_sauveteur);

        const result2 = await axiosConfig({
          method: 'post',
          url: "sauveteur_sauvetage/sauveteur",
          data: {
            id_sauveteur : res.id_sauveteur
          }
        })

        if (result2.data.err) {
          console.log(result.data);
        } else {
          setMission(result2.data);
        }
      }
    } catch (e) { console.log(e) }
  }

  useEffect(() => {
    if (id != -1 && id) getObj();
  }, [id])

  const DisplayMission = () => {
    if (!mission.length) {
      return <p>Ce sauveteur n'a pas encore effectué de missions</p>
    }
    return mission.map((item, index) => {
      return <Link key={index} to={'sauvetage/' + item.id_sauvetage}>
        {item.nom}
      </Link>
    });
  }

  const post = async () => {
    try {
      const result = await axiosConfig({
        method: 'post',
        url: `sauveteur/`,
        data: {
          id_sauveteur: id_sauveteur,
          nom: nom,
          prenom: prenom,
          date_de_mort: date_de_mort,
          date_de_naissance: date_de_naissance,
          legion: legion,
          verif: 0,
          id_utilisateur: user.id
        } 
      })

      if (result.data.err) {
        console.log(result.data);
      } else {
        history.push("/sauveteur")
      }
    } catch (e) { console.log(e) }
  }

  const update = async () => {
    try {
      const result = await axiosConfig({
        method: 'put',
        url: `sauveteur/`,
        data: {
          id_sauveteur: id_sauveteur,
          nom: nom,
          prenom: prenom,
          date_de_mort: date_de_mort,
          date_de_naissance: date_de_naissance,
          legion: legion,
          verif: admin,
          id_utilisateur: user.id
        } 
      })

      if (result.data.err) {
        console.log(result.data.err);
      } else {
        getObj();
      }
    } catch (e) { console.log(e) }
  }

  const submit = (e) => {
    e.preventDefault();
    if (id != -1) {
      update();
    } else {
      post();
    }
  }

  return (
    <div className={(admin || id == -1) ? "page-form" : "page-form page-form-info"}>
      <form onSubmit={submit}>
        <h2>Information sur {prenom}</h2>

        <RiUserHeartFill />
        <section>
          <div>
            <label htmlFor="prenom">Prénom</label>
            <input 
              type="text" 
              id="nom" 
              required 
              placeholder="prénom"
              autocomplete="off"
              value={prenom}
              onChange={e => setPrenom(e.target.value)}
              disabled={!admin}
            />
          </div>
          <div>
            <label htmlFor="nom">Nom</label>
            <input 
              type="text" 
              id="nom" 
              required 
              placeholder="nom"
              autocomplete="off"
              value={nom}
              onChange={e => setNom(e.target.value)}
              disabled={!admin}
            />
          </div>
          <div>
            <label htmlFor="date_de_naissance">Date de naissance</label>
            <input 
              type="text" 
              id="date_de_naissance" 
              required 
              placeholder="date de naissance"
              autocomplete="off"
              value={date_de_naissance}
              onChange={e => setDate_de_naissance(e.target.value)}
              disabled={!admin}
            />
          </div>

          <div>
            <label htmlFor="date_de_mort">Date de mort</label>
            <input 
              type="text" 
              id="date_de_mort" 
              required 
              placeholder="date de mort"
              autocomplete="off"
              value={date_de_mort}
              onChange={e => setDate_de_mort(e.target.value)}
              disabled={!admin}
            />
          </div>

          <div>
            <label htmlFor="date_de_mort">Légion</label>
            <input
              type="checkbox"
              checked={legion}
              onChange={() => setLegion(!legion)}
            />
          </div>
        </section>

        <div className="buttons">
        {(admin && id != -1) && <button className="btn" type="submit">Modifier</button>}
        {(id == -1) && <button className="btn" type="submit">Créer</button>}
        <Retour />
        </div>
      </form>

      {(id != -1) && <form onSubmit={submit}>
        <h2>Liste des sauvetages auquel {prenom} à participer :</h2>

        <DisplayMission />
      </form>}
    </div>
    
  );
}

export default Info;