import React from 'react'
import { Link, useParams } from 'react-router-dom';
import { useState, useEffect } from 'react';
import axiosConfig from 'axiosConfig';
import { RiUserHeartFill } from "react-icons/ri";
import TabButton from './TabButton/TabButton';
import { GiShoonerSailboat as Bato} from 'react-icons/gi'
import './ResultatsRecherche.scss'

const ResultatsRecherche = () => {

    const {
        keyword,
        filter,
        mindate,
        maxdate
    } = useParams();

   const searchData = {
    keyWord: keyword,
    minDate: parseInt(mindate),
    maxDate: parseInt(maxdate)
    }

    const [tab, setTab] = useState(0);

    const [resultB, setResultB] = useState([]);
    const [resultS, setResultS] = useState([]);
    const [resultM, setResultM] = useState([]);


    const fetchData = () => {
        try {
            switch(parseInt(filter)){
                case(0):
                    searchBateau();
                    searchSauvetage();
                    searchSauveteur();
                    break;
                case(1):
                    searchSauveteur();
                    setTab(1);
                    break;
                case(2):
                    searchBateau();
                    setTab(2);
                    break;
                case(3):
                    searchSauvetage();
                    setTab(0);
                    break;
            }
        } catch(e) {
            console.log(e)
        }
    }

    const searchBateau = async () => {
        const result = await axiosConfig({
            method: 'post',
            url: `recherche/bateau`,
            data: searchData
        })

        setResultB(result.data);
    }

    const searchSauveteur = async () => {
        const result = await axiosConfig({
            method: 'post',
            url: `recherche/sauveteur`,
            data: searchData
        })

        setResultS(result.data);
    }

    const searchSauvetage = async () => {
        const result = await axiosConfig({
            method: 'post',
            url: `recherche/sauvetage`,
            data: searchData
        })

        setResultM(result.data);
    }


    useEffect(() => {

        fetchData()
    }, [])

    return (
        <div className="page">
            <div className="search-tabs">
            {resultM.length > 0 && <div className={tab == 0 ? "btn btn-select" : "btn"} onClick={() => setTab(0)}>
                Sauvetages
            </div>}
            
            {resultM.length > 0 && <div className={tab == 1 ? "btn btn-select" : "btn"} onClick={() => setTab(1)}>
                Sauveteur
            </div>}


            {resultM.length > 0 && <div className={tab == 2 ? "btn btn-select" : "btn"} onClick={() => setTab(2)}>
                Bateaux
            </div>}
  

            </div>

            <div className="search-results">

                {tab === 0 && resultM.map((item, index) => {
                    return <Link className={item.verif ? "card" : " card card-verif"} to={`/sauvetage/` + item.id_sauvetage} key={index}>
                    <RiUserHeartFill />

                    <p>{item.nom}</p>

                    <Link className="btn" to={`sauveteur/` + item.id_sauvetage}>Voir plus</Link>

                </Link>
                })}

                {tab === 1 && resultS.map((item, index) => {
                return <Link className={item.verif ? "card" : " card card-verif"} to={`/sauveteur/` + item.id_sauveteur} key={index}>
                    <RiUserHeartFill />

                    <p>{item.nom}</p>

                    <Link className="btn" to={`sauveteur/` + item.id_sauveteur}>Voir plus</Link>

                </Link>
                })}

                {tab === 2 && resultB.map((item, index) => {
                    return <Link className={item.verif ? "card" : " card card-verif"} to={`/bateau/` + item.id_bateau} key={index}>
                    <Bato />
            
                    <p>{item.nom}</p>

            
                    <Link className="btn" to={`bateau/` + item.id_bateau}>Voir plus</Link>
            
                  </Link>
                })}

            </div>

        </div>
        
    )
}

export default ResultatsRecherche
