import './Account.scss';
import { useState, useEffect, useContext } from 'react';
import { Link } from 'react-router-dom';
import { UserContext } from 'context';
import axiosConfig from 'axiosConfig';

function Account() {
  const [user] = useContext(UserContext);

  const [nom, setNom] = useState(user?.nom);
  const [prenom, setPrenom] = useState(user?.prenom);
  const [email, setEmail] = useState(user?.email);
  const [adresse, setAdresse] = useState(user?.adresse);
  const [tel, setTel] = useState(user?.tel);

  useEffect(() => {
    document.title = "Mon compte";
  }, []);


  const submit = (event) => {
    event.preventDefault();
 
  }


  return (
    <div className="page-form login">
      <form onSubmit={submit}>
              <h2>Vos information</h2>

              <section>
                <div>
                  <label htmlFor="mail">Mail</label>
                  <input 
                    type="text" 
                    id="mail" 
                    required 
                    placeholder="prenom.nom"
                    autoComplete="username"
                    value={email}
                    onChange={e => setEmail(e.target.value)}
                  />
                </div>

              <div>
                <label htmlFor="prenom">Prénom</label>
                <input 
                  required 
                  type="text" 
                  id="nom" 
                  placeholder="Votre Prénom" 
                  autoComplete="current-name"
                  value={prenom}
                  onChange={e => setPrenom(e.target.value)}
                />
              </div>

              <div>
                <label htmlFor="nom">Nom</label>
                <input 
                  required 
                  type="text" 
                  id="nom" 
                  placeholder="Votre Nome" 
                  autoComplete="current-name"
                  value={nom}
                  onChange={e => setTel(e.target.nom)}
                />
              </div>

              <div>
                <label htmlFor="tel">Téléphone</label>
                <input 
                  required 
                  type="tel" 
                  id="tel" 
                  placeholder="Téléphone" 
                  autoComplete="current-tel"
                  value={tel}
                  onChange={e => setTel(e.target.value)}
                />
              </div>

              <div>
                <label htmlFor="adresse">Votre adresse</label>
                <input 
                  required 
                  type="text" 
                  id="nom" 
                  placeholder="Votre Adresse" 
                  autoComplete="current-address"
                  value={adresse}
                  onChange={e => setAdresse(e.target.value)}
                />
              </div>

              </section>

              <button className='btn' type="submit">Modifier mes informations</button>
      </form>

    </div>
  );
}

export default Account;
